.section {
  // outline: 1px solid orange;
  padding-top: 30px;
  padding-bottom: 30px;
  border-bottom: 2px solid var(--border-primary);

  .title {
    text-align: center;
  }

  &.html {
    padding-top: 0;
  }

  &.section-even {
    background-color: rgb(248, 248, 248);
    border-left: 2px solid var(--border-primary);
  }
  &-description {
    margin-bottom: 20px;
    text-align: center;
    font-size: 24px;
    line-height: 1;
  }
}
